import { FC, useMemo } from "react";
import { Table } from "antd";
import { CalcBaseGridProps } from "../types";
import {
  HighBinaryColumnKeys,
  highColumns,
  LowBinaryColumnKeys,
  lowColumns
} from "../constants/";
import { getData } from "../utils";

//import styles from "../styles/index.less";

export const CalcBaseGrid: FC<CalcBaseGridProps> = (props) => {
  const { isHigh, calcBase } = props;

  const columns = isHigh ? highColumns : lowColumns;
  const columnKeys = isHigh ? HighBinaryColumnKeys : LowBinaryColumnKeys;

  const dataSource = useMemo(() => {
    return getData(columnKeys, calcBase);
  }, [calcBase]);

  return (
    <Table
      //className={styles["container"]}
      columns={columns}
      dataSource={dataSource}
      scroll={{ x: "max-content" }}
      pagination={false}
      bordered
    />
  );
};
