import { FC, useState } from "react";
import { Divider, Input } from "antd";
import { CalcBaseGrid } from "./CalcBaseGrid";
import { CalcBaseParseProps } from "../types";

function changeValue(value: string, setValue: Function) {
  if (!value) {
    setValue(undefined);
    return;
  }

  if (/^\d*$/.test(value)) {
    setValue(Number(value));
    return;
  }

  setValue(undefined);
}

const CalcBaseParse: FC<CalcBaseParseProps> = (props) => {
  const { isHigh } = props;
  const placeholder = isHigh ? "高位" : "低位";

  const [value, setValue] = useState<number>();

  const onChange = (event: any) => {
    changeValue(event.target.value, setValue);
  };

  return (
    <>
      <Input.Group
        style={{
          display: "flex",
          justifyContent: "flex-end",
          marginBottom: "24px"
        }}
      >
        <Input
          style={{ width: "20%" }}
          placeholder={placeholder}
          value={value}
          onChange={onChange}
        />
      </Input.Group>
      <CalcBaseGrid calcBase={value} isHigh={isHigh} />
    </>
  );
};

export const CalcBase: FC = () => {
  return (
    <>
      <CalcBaseParse isHigh />
      <Divider />
      <CalcBaseParse />
    </>
  );
};
